@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Material Icons);
* {
  box-sizing: border-box;
  line-height: 100%;
  vertical-align: top; }

*:focus,
*:active {
  outline: none; }

*::-moz-focus-inner {
  border: 0; }

a {
  text-decoration: none; }

.input {
  width: 100%;
  height: 40px;
  border: 1px solid #888;
  background: #FFF;
  font-size: 16px;
  font-family: "Open Sans";
  color: #000;
  line-height: normal;
  padding: 0 7px;
  -webkit-transition: .3s;
  transition: .3s; }

.input:focus {
  border-color: #4091EC; }

.input.select {
  -moz-appearance: none;
  -webkit-appearance: none;
  -o-appearance: none;
  cursor: pointer;
  text-indent: 0.01px;
  text-overflow: "";
  padding: 0 7px;
  background: #FFF; }

.input.select option {
  padding: 10px; }

.input.textarea {
  resize: none;
  padding: 7px 10px;
  height: 100px; }

.check, .radio {
  display: inline-block;
  vertical-align: middle;
  margin: 0; }

.clear_button {
  border: 0;
  background: transparent;
  margin: 0;
  padding: 0;
  font-family: "Open Sans";
  cursor: pointer; }

.button {
  height: 40px;
  width: auto;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  font-size: 16px;
  color: #FFF;
  font-family: "Open Sans";
  line-height: 40px;
  margin: 0;
  padding: 0 15px;
  border: 0;
  background: #4091EC;
  -webkit-transition: .3s background;
  transition: .3s background; }
  .button.canceloption {
    background: #f6f6f6; }

.button:disabled,
.button:hover:disabled {
  background: #f6f6f6;
  cursor: default; }

.button:hover {
  background: #6eacf1; }
  .button:hover.canceloption {
    background: white; }

ul, dl, li, dd, dt, p {
  margin: 0;
  padding: 0; }

li, dd {
  display: block; }

pre {
  font-size: 14px;
  border: 1px solid #f6f6f6;
  padding: 25px;
  margin: 25px;
  background: #FFF; }

i {
  font-style: normal; }

h1, h2, h3 {
  margin: 0; }

body {
  padding: 0;
  margin: 0;
  background: #4091EC;
  font-size: 12px;
  font-family: "Open Sans";
  color: #000; }

.container {
  width: 290px;
  margin: auto; }

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0; }

input[type=number] {
  -moz-appearance: textfield; }

.tracker-form {
  background: #f6f6f6;
  padding: 30px;
  margin: 30px 0;
  font-size: 0; }
  .tracker-form .two-cols {
    display: inline-block;
    vertical-align: middle;
    width: calc(50% - 7.5px);
    padding-bottom: 30px; }
    .tracker-form .two-cols .form-label {
      display: block;
      padding-bottom: 5px;
      font-size: 12px; }
    .tracker-form .two-cols > button {
      display: inline-block;
      vertical-align: middle;
      width: 20px;
      padding: 0; }
    .tracker-form .two-cols > input {
      display: inline-block;
      vertical-align: middle;
      width: calc(100% - 40px); }
    .tracker-form .two-cols:nth-of-type(2n) {
      margin-left: 15px; }
  .tracker-form .form-foot .rank {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    font-size: 12px;
    text-transform: uppercase; }
    .tracker-form .form-foot .rank span {
      font-weight: 700;
      color: #1fbba6; }
      .tracker-form .form-foot .rank span.pow {
        color: #f23564; }
      .tracker-form .form-foot .rank span.tech {
        color: #4091EC; }
  .tracker-form .form-foot .button-holder {
    display: inline-block;
    vertical-align: top;
    width: 50%;
    text-align: right; }

